import styled from "styled-components";

export const Section = styled.section`
	min-height: 30rem;
	position: relative;
	&.intro {
		background: #76b0ad;
	}
	&.self_interview {
		background: #9b8281;
	}
	&.skills {
		background: #6182b8;
	}
	&.portfolio {
		background: #6b8fa1;
	}
	&::before {
		content: "";
		display: block;
		width: calc(100% - 4rem);
		height: calc(100% - 4rem);
		position: absolute;
		top: 2rem;
		bottom: 2rem;
		left: 2rem;
		right: 2rem;
		border: 1px solid #fff;
	}
`;
export const SectionTitle = styled.h2`
	font-size: 20px;
	color: #fff;
	position: absolute;
	top: 2rem;
	left: 2rem;
	font-weight: 600;
`;
